<template>
    <div class="m-home-view">
        <Search />

        <WikiPanel :border-none="true">
            <template slot="head-title">
                <i class="el-icon-collection"></i>
                <span>热门任务</span>
            </template>
            <template slot="body">
                <div class="m-hot-slots">
                    <el-carousel
                        height="66px"
                        direction="vertical"
                        indicator-position="none"
                        v-if="hots && hots.length"
                    >
                        <el-carousel-item
                            v-for="(items, key) in hots"
                            :key="key"
                            class="m-carousel"
                        >
                            <el-row :gutter="20">
                                <template v-for="(item, k) in items">
                                    <el-col :md="8" v-if="item" :key="k">
                                        <router-link
                                            class="u-item"
                                            :to="{name:'view',params:{source_id:item.id}}"
                                        >
                                            <div class="u-icon">
                                                <img :src="hot_icon" />
                                            </div>
                                            <div class="m-carousel-content">
                                                <div class="u-title" v-text="item.Name"></div>
                                                <div class="u-desc">
                                                    <span>【{{item.EndMapName}}】</span>
                                                </div>
                                            </div>
                                        </router-link>
                                    </el-col>
                                </template>
                            </el-row>
                        </el-carousel-item>
                    </el-carousel>
                    <div v-else style="text-align:center">😂 暂无热门任务</div>
                </div>
            </template>
        </WikiPanel>

        <WikiPanel :border-none="true">
            <template slot="head-title">
                <i class="el-icon-time"></i> 
                <span>最近更新</span>
            </template>
            <template slot="body">
                <div class="m-posts">
                    <div class="m-post" v-for="(post, key) in newest_posts" :key="key">
                        <div class="m-head">
                            <div class="m-source">
                                <div class="u-source">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path d="M507.513 94.223L417.779 4.488A15.269 15.269 0 00406.618.004a15.315 15.315 0 00-10.958 4.957L271.069 140.675c-3.273-25.551-25.141-45.367-51.568-45.367-28.677 0-52.006 23.33-52.006 52.006 0 7.732 3.334 17.808 8.116 28.297-10.489-4.782-20.565-8.116-28.297-8.116-28.676 0-52.006 23.329-52.006 52.006 0 26.425 19.817 48.295 45.367 51.568L4.961 395.659a15.323 15.323 0 00-.473 22.119l89.734 89.734a15.321 15.321 0 0022.119-.472l187.001-203.697 203.697-187.001a15.324 15.324 0 00.474-22.119zM219.502 125.95c11.781 0 21.364 9.584 21.364 21.364 0 .864-.452 9.206-16.112 35.728a397.246 397.246 0 01-5.252 8.637 401.184 401.184 0 01-5.253-8.637c-15.66-26.522-16.112-34.864-16.112-35.728 0-11.781 9.584-21.364 21.365-21.364zm-93.553 93.551c0-11.781 9.584-21.364 21.364-21.364.864 0 9.206.452 35.728 16.112a398.71 398.71 0 018.663 5.269c-18.638 11.574-37.679 21.348-44.392 21.348-11.778 0-21.363-9.584-21.363-21.365zM104.584 474.54l-67.121-67.121L219.05 240.716l52.238 52.238L104.584 474.54zm188.369-203.254l-52.238-52.238L407.418 37.462l67.121 67.122-181.586 166.702z"/></svg>
                                    <router-link
                                        class="u-name"
                                        :to="{name: 'view', params: { source_id: post.source_id }}"
                                        v-text="post.title"
                                    ></router-link>
                                </div>
                                <div
                                    class="u-remark"
                                    v-if="post.remark"
                                    v-text="'📑 ' + post.remark"
                                ></div>
                            </div>
                            <div class="m-user">
                                <div class="u-author">
                                    <img
                                        class="u-icon"
                                        :src="post.user_avatar"
                                        :alt="post.user_nickname"
                                    />
                                    <a
                                        :href="post.user_id ? author_url(post.user_id) : null"
                                        class="u-name"
                                        v-text="post.user_nickname"
                                    ></a>
                                </div>
                                <div class="u-updated" v-text="ts2str(post.updated)"></div>
                            </div>
                        </div>
                        <div class="m-body">
                            <div
                                class="u-excerpt"
                                :to="{ name: 'view', params: { source_id: post.source_id } }"
                                v-html="ellipsis(post.excerpt)"
                            ></div>
                        </div>
                    </div>
                </div>
            </template>
        </WikiPanel>
    </div>
</template>

<script>
const { JX3BOX } = require("@jx3box/jx3box-common");
import { feedback } from "@jx3box/jx3box-common/data/jx3box.json";
import Search from "../components/Search";
import WikiPanel from "@jx3box/jx3box-common-ui/src/wiki/WikiPanel";
import { WikiPost } from "@jx3box/jx3box-common/js/helper";
import { get_list } from "../service/quest.js";
import { getStatRank } from "@jx3box/jx3box-common/js/stat";
import { authorLink, ts2str, iconLink } from "@jx3box/jx3box-common/js/utils";

export default {
    name: "Home",
    props: [],
    data() {
        return {
            feedback,
            menu_groups: {},
            hots: null,
            hot_views: null,
            hot_icon: JX3BOX.__iconPath + "icon/388.png",
            jargons: [],
            newest_posts: null,
        };
    },
    methods: {
        icon_url: iconLink,
        author_url: authorLink,
        ts2str,
        chuck(arr, number = 3) {
            let output = [];
            for (let i = 0; i < arr.length; i += number) {
                output.push(arr.slice(i, i + number));
            }
            return output;
        },
        ellipsis(value) {
            value = value ? value.trim() : "";
            if (value.length > 100) {
                return value.slice(0, 100) + "...";
            }
            return value;
        },
        loadHotPlots: function () {
            getStatRank("quest", "views", 18).then((res) => {
                res = res.data;
                this.hot_views = [];
                let source_ids = [];
                res.forEach((item) => {
                    if (item.name.startsWith("quest")) {
                        let id = item.name.split("-").pop();
                        source_ids.push(id);
                        this.hot_views[id] = item.value;
                    }
                });

                get_list({ ids: source_ids }).then((res) => {
                    res = res.data;
                    // 按照长度分批
                    if (res.code === 200)
                        this.hots = this.chuck(Object.values(res.data.data));
                });
            });
        },
        loadNewestPost() {
            // 获取最新帖子列表
            WikiPost.newests("quest").then((res) => {
                res = res.data;
                if (res.code === 200) this.newest_posts = res.data.newest;
            });
        },
        loadData: function () {
            this.loadHotPlots();
            this.loadNewestPost();
        },
    },
    created() {
        this.loadData();
    },
    components: {
        Search,
        WikiPanel,
    },
};
</script>

<style lang="less">
@import "../assets/css/views/home.less";
</style>
